<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'

  Vue.component('loading', Loading)

  export default {
    name: 'suggestion',
    data () {
      return {
        suggestion: {},
        id: '',
        title: '',
        order: '',

        loaded: false
      }
    },
    created () {
      api.get(`suggestions/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.suggestion = res
        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'suggestions' })
      })
    }
  }
</script>

<template>
  <div class="widget-body">
    <loading v-if="!loaded"/>
    <div class="row" v-else>
      <div class="col">
        <vuestic-widget :headerText="suggestion.title">

          <div class="row show-buttons-area mb-3">
            <router-link
              :to="{ name: 'suggestions' }"
              class="btn btn-info btn-micro"
            >
              {{'buttons.back' | translate}}
            </router-link>
          </div>

          <div class="row row-reverse">
            <div class="col-6">
              <ul class="rtl-list">
                <li style="">
                  <b class="expand-icon">{{ $t('suggestion.name') }}</b> : 
                  {{ suggestion.name }}
                </li>
                <li>
                  <b class="expand-icon">{{ $t('suggestion.phone') }}</b> : 
                  {{ suggestion.phone }}
                </li>
                <li>
                  <b class="expand-icon">{{ $t('suggestion.email') }}</b> : 
                  {{ suggestion.email }}
                </li>
                <li>
                  <b class="expand-icon">{{ $t('suggestion.section') }}</b> : 
                  {{ suggestion.section }}
                </li>
              </ul>
            </div>
            <div class="col-6" style="direction: rtl;text-align: right;">
              {{ suggestion.content }}
            </div>
          </div>
        </vuestic-widget>
      </div>
    </div>
  </div>
</template>
